$chunkWidth: 25px;
$chunksGap: 15px;

.monthRow {
  transform: translateX(0);

  .offsetController {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    transition: transform 0.5s ease-in-out;
    transform: translateX(0);
  }
}

:export {
  monthChunkSize: $chunkWidth;
  monthChunksGap: $chunksGap;
}
