.leftBlock {
  z-index: 10;
  height: 100%;

  display: flex;
  flex-direction: column;

  .chunk {
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    text-align: center;

    box-shadow: 1px 0px 0px gray;

    &.first {
      visibility: hidden;
      box-shadow: none;
    }

    &.blank {
      visibility: hidden;
    }
  }
}
