.scrollable {
  transform: translate3d(0px, 0px, 0px);
  transition: transform 0.5s ease-in-out;
}

.scrollingLayer {
  position: absolute;
  width: 100%;
  height: 100%;

  &.scrollIsJustReset {
    transition: none;
  }

  display: grid;
  grid-template-rows: min-content 1fr;

  .topBlock {
  }

  .bottomBlock {
    position: relative;
  }
}
